// src/components/Testimonials.jsx
import React from "react";

const Testimonials = () => {
  return (
    <section className="py-20 bg-gray-200 text-center">
      <h2 className="text-4xl font-semibold text-gray-900 mb-8">What Our Clients Say</h2>
      <div className="max-w-3xl mx-auto">
        <div className="bg-white p-8 rounded-lg shadow-lg">
          <p className="text-lg italic text-gray-700">
            "Intelion's AI solutions transformed our business. A true game changer! Their insights have
            enabled us to grow and make smarter decisions."
          </p>
          <h4 className="mt-6 text-xl font-semibold text-gray-800">John Doe</h4>
          <p className="text-gray-600">CEO, TechCompany</p>
        </div>
      </div>
    </section>
  );
};

export default Testimonials;
