// src/components/Contact.jsx
import React from "react";

const Contact = () => {
  return (
    <section className="py-20 text-center bg-blue-600 text-white">
      <h2 className="text-4xl font-semibold mb-4">Get In Touch</h2>
      <p className="text-xl mb-6">We would love to hear from you and discuss how Intelion can help your business.</p>
      <a
        href="mailto:contact@intelion.com"
        className="bg-green-500 text-white py-3 px-6 rounded-full text-lg hover:bg-green-600"
      >
        Contact Us
      </a>
    </section>
  );
};

export default Contact;
